.Grid {
  padding: 5px;
  margin: 10px;
}

.Grid-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Grid-tile {
  width: 100px;
  height: 100px;
  line-height: 100px;
  -ms-flex-align: center;
      align-items: center;
  border: solid 1px #ccc;
  border-radius: 4px;
  text-align: center;
  margin: 4px;
  font-size: 30px;
  font-weight: bold;
  color: #666;
  background: rgba(238, 228, 218, 0.35);
  -webkit-transition: background 0.5s;
  -o-transition: background 0.5s;
  transition: background 0.5s;
}

.tile-2 {
  background: rgb(238, 228, 218);
}
.tile-4 {
  background: rgb(237, 224, 200);
}
.tile-8 {
  color: #f9f6f2;
